.whole-page {
  background-color: black;
  height: 80vh;
  padding-top: 20vh;
  background-image: url("Pics/HK-outside.png");
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-flow: column nowrap;
}

.whole-page {
  margin: 0;
}
.whole-page > div {
  text-align: center;
  color: white;
  font-size: small;
  word-spacing: 2px;
  letter-spacing: 3px;
  padding-top: 5vh;
}

.button {
  background-color: rgb(0, 0, 0, 0);
  color: white;
  text-transform: uppercase;
  padding: 5px 20px 5px 20px;
  font-size: x-small;
  letter-spacing: 2px;
  border: 2px solid white;
}

.button:hover {
  background-color: rgb(95, 214, 95, 0.5);
  border: 2px solid rgb(95, 214, 95, 0.5);
  cursor: pointer;
}

.Team {
  height: 100%;
  overflow-y: visible;
}

.Team .roster {
  height: 100%;
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}
@media (min-width: 1076px) {
  .Team .roster .card {
    height: 240px;
    width: 50%;
  }
}

.Team .roster .card {
  display: inline-flex;
  border-bottom: 1px solid black;
  background-size: 75%;
  background-repeat: no-repeat;
  background-position: right;
  position: relative;
}
.Team .roster .card:nth-of-type(even) {
  background-color: rgb(106, 215, 115);
  color: black;
}
.Team .roster .card:nth-of-type(odd) {
  background-color: rgb(147, 148, 232);

  color: white;
}

.Team .mask:before {
  position: absolute;
  content: " ";
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.2;
  background-color: #000;
}

.roster .card {
  height: 15vh;
  width: 100vh;
  justify-content: center;
  margin: auto;
  align-items: center;
  font-size: xx-large;
  letter-spacing: 5px;
}

.bottom-nav {
  height: 19vh;
  width: 33vh;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid black;
  border-right: none;
  border-left: none;
}

.more-nav {
  display: flex;
  flex-flow: row wrap;
  margin: auto;
}

.bottom-nav:hover {
  cursor: pointer;
}

.bg-letter {
  position: absolute;
  font-size: 20vh;
  opacity: 0.2;
}

.h1-class {
  font-size: 5vh;
  text-align: center;
  color: white;
  background-color: rgb(120, 121, 188);
  height: 100%;
}
